import helper from '../../common/helper.js';
export default {
	data() {
		return {
			listArray:'',
			tishi:''
		};
	},
	onLoad() {
		// this.sclist()
		
	},
	onShow(){
		// this.sclist()
	},
	methods: {
		// 获取任务的id
		openrwid(e) {
			uni.navigateTo({
				url: '../task/Taskdetails?rwid=' + e.currentTarget.dataset.rwid,
			});
		},
		// 收藏任务列表
		sclist() {
			uni.showLoading({
				title: "加载中....",
				mask: true
			})
			uni.request({
				url: helper.websiteUrl + 'task/collectiontasklist',
				method: 'GET',
				data: helper.postdata({
					"page":"1",
					"num":"10",
					"userid": helper.getstate().userid,
				}),
				success: res => {
					if (res.data.code == 200) {
						this.listArray = res.data.data.baseinfo;
						if (this.listArray == "") {
							this.tishi = '暂无收藏记录'
						} else{
							this.tishi = ''
						}
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					uni.hideLoading();
				},
				fail: (res) => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请下拉刷新重试！'
					});
				}
			});
		}
	}
}
